

















































































































@import "@/styles/util/util.scss";

.wrapper {
    background: #FFF;
}

.job-list {
    table.table {
        @include rem((padding: 60px 0));
        tbody {
            tr > td {
                vertical-align: middle;
            }
            tr {
                height: 80px;
                td {
                    &.jobtitle {
                        a {
                            color: $color-red-t2w;
                            font-weight: bold;
                            cursor: pointer;
                        }
                    }
                    p {
                        @include rem(font-size, 14px);
                        max-width: 600px;
                    }
                }
                &:hover {
                    background: $color-gray-light;
                }
            }
        }
    }
}
